<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 电影管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                        type="primary"
                        icon="el-icon-delete"
                        class="handle-del mr10"
                        @click="handleAdd"
                >添加</el-button>

                <el-select v-model.number="query.state" placeholder="请选择">
                    <el-option v-for="item in GoodsStateAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="query.goodsName" placeholder="商品名称" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Seq" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="GoodsName" label="商品名称"></el-table-column>
                <el-table-column label="商品价格">
                    <template slot-scope="scope">{{scope.row.Price}}</template>
                </el-table-column>
                <el-table-column label="兑换积分">
                    <template slot-scope="scope">{{scope.row.Point}}</template>
                </el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">{{scope.row.State|GoodsStateName}}</template>
                </el-table-column>


                <el-table-column label="操作" width="140" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                class="red"
                                @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>


        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="90%">
            <el-form   :inline="true"   ref="form" :model="form" :rules="rules" label-width="100px">
                <el-form-item label="商品名称" prop="GoodsName">
                    <el-input v-model="form.GoodsName"></el-input>
                </el-form-item>

                <el-form-item label="状态"  prop="State">
                    <el-select v-model.number="form.State" placeholder="请选择">
                        <el-option v-for="item in GoodsStates" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="序号">
                    <el-input-number class="loose-input"  v-model="form.Seq"   :step="1" :min="0" ></el-input-number>
                </el-form-item>
                <el-form-item label="商品价格">
                    <el-input-number class="loose-input"  v-model="form.Price" :precision="2" :step="0.01" :min="0" ></el-input-number>
<!--                    <el-input v-model.number="form.Price"></el-input>-->
                </el-form-item>
                <el-form-item label="兑换积分">
<!--                    <el-input v-model.number="form.Point"></el-input>-->
                    <el-input-number class="loose-input"  v-model="form.Point"   :step="1" :min="0" ></el-input-number>

                </el-form-item>
                <el-form-item label="数量">
                    <el-input-number class="loose-input"  v-model="form.GoodsCount"   :step="1" :min="0" ></el-input-number>

                </el-form-item>
                <el-form-item label="售出数量">
                    <el-input-number class="loose-input"  v-model="form.QuantitySold"   :step="1" :min="0" ></el-input-number>
                </el-form-item>
                <el-divider></el-divider>

                <el-form-item label="商品标题">
                    <el-input v-model="form.Title"></el-input>
                </el-form-item>
                <el-form-item label="摘要">
                    <div>
                    <el-input type="textarea" style="width: 400px" v-model="form.Summary"></el-input>
                    </div>
                </el-form-item>
                <el-form-item label="商品图片" prop="UpdatedUrl">
                    <!--                    <el-input v-model="form.GoodsPicUrl" readonly></el-input>-->
                    <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :auto-upload="false"
                            :on-change="handleChangeGoodsPicUrl"
                            :show-file-list="false" >
                        <img v-if="form.GoodsPicUrl" :src="form.GoodsPicUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <button v-if="form.GoodsPicUrl" @click="delGoodsPicUrl()"  >删除</button>

                </el-form-item>
                <el-divider></el-divider>


                <el-form-item label="内容">
<!--                        <quill-editor ref="myTextEditor" v-model="form.Content" :options="editorOption"></quill-editor>-->
                    <tinymce ref="editor" :inivalue="CCCCC" v-model="form.Content" :height="300" />
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import { request } from '../../utils/http';
    import Tinymce from '../../components/Tinymce'

    export default {
        name: 'GoodsList',

        data() {
            return {
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                GoodsStateAll:[
                    {name:'全部',value:0},
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                GoodsStates:[
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                query: {
                    state: 0,
                    goodsName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                rules: {

                },
                CCCCC:'',

                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {Content:''},
                idx: -1,
                id: -1,
                GoodsPicUrlFile:{},
            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;

        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        components: {
            Tinymce
        },
        methods: {
            delGoodsPicUrl(){
                this.form.GoodsPicUrl = "";
                this.GoodsPicUrlFile = '';
            },
            handleChangeGoodsPicUrl(file, fileList) {
                this.form.GoodsPicUrl = URL.createObjectURL(file.raw);
                this.GoodsPicUrlFile = file.raw;
                console.log("handleChangeGoodsPicUrl A");
            },

            // 获取 easy-mock 的模拟数据
            getData() {
                request({url: 'filmbll/getgoodslist', method: 'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 删除操作
            handleDelete(index, row) {
                // 二次确认删除
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        var data = {
                            id:row.Id
                        }
                        request({
                            url:'filmbll/delgoodsinfo', method: 'post',
                            data:data
                        }).then((res)=>{
                            console.log(res);
                            if (res.code==200){
                                this.$message.success('删除成功');
                                this.tableData.splice(index, 1);
                                this.getData();
                            } else {
                                this.$message.error('删除失败：'+res.msg);
                            }
                        });

                    })
                    .catch(() => {});
            },
            // 编辑操作
            handleEdit(index, row) {
                var data = {
                    id:row.Id
                }
                request({
                    url:'filmbll/getgoodsdetail',  method:'post',
                    data:data
                }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.idx = index;
                        this.form = res.obj;
                        this.CCCCC =  res.obj.Content;

                        // this.$refs.editor.setContent("aaaa") ;
                        this.editVisible = true;

                    } else {
                        this.$message.error('数据变动，无法编辑：'+res.msg);
                    }
                });
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={
                    Id          :0,
                    GoodsName   :'',
                    State       :1,
                    GoodsPicUrl :'',
                    Point       :1,
                    Price       :1,
                    Title       :'',
                    Summary     :'',
                    Content     :'0',
                    Seq         :0,
                    GoodsCount  :1,
                    QuantitySold:0,
                }
                this.form = aFilm;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                this.form.GoodsPicUrlFile = this.GoodsPicUrlFile;
                //this.form.Content = this.CCCCC;
                if (this.idx===-1) {
                    this.form.id = 0;
                    request({
                       url: 'filmbll/addgoodsinfo',  method:'file',
                        data:this.form
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`添加成功`);
                            this.getData();
                        } else {
                            this.$message.error('添加失败：'+res.msg);
                        }
                    });
                } else {
                    request({
                        url:'filmbll/updategoods',  method:'file',
                        data:this.form
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                            this.$set(this.tableData, this.idx, this.form);
                        } else {
                            this.$message.error('更新失败：'+res.msg);
                        }
                    });

                }
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>

    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }

    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 256px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 256px;
        height: 178px;
        display: block;
    }
</style>
